<template>
	<div class="">
		<p class="pages_tab">ÂΩìÂâçÈ°µÔºöÊ≠åÂçïÁÆ°ÁêÜ<i class="el-icon-arrow-right"></i>Ê¶úÂçï</p>
		<!-- Á≠õÈÄâÂå∫Âüü -->
		<el-form label-width="80px" :inline="true">
				<el-form-item label="Ê≠åÂçïÁä∂ÊÄÅ">
					<el-select v-model="Sstatus" clearable placeholder="Ê≠åÂçïÁä∂ÊÄÅÁ≠õÈÄâ">
						<el-option
							key="1"
							label="Â∑≤‰∏äÁ∫ø"
							value="1">
						</el-option>
						<el-option
							key="0"
							label="Â∑≤‰∏ãÁ∫ø"
							value="0">
						</el-option>
					</el-select>
				</el-form-item>
		<el-form-item label="Ê≠åÊõ≤ÂêçÁß∞">
			<el-input placeholder="Ê≠åÊõ≤ÂêçÁß∞" v-model="Stitle"></el-input>
		</el-form-item>
		<el-button type="success" icon="el-icon-search" @click="handleCurrentChange()">ÊêúÁ¥¢</el-button>
		</el-form>
		<el-table
			align="center"
			header-align="center"
			:data="songList"
			height="calc(100vh - 270px)"
			:row-class-name="tableRowClassName">
			<el-table-column
				prop="id"
				label="ID">
			</el-table-column>
				<el-table-column
					prop="track_id"
					label="Ê≠åÊõ≤ID">
				</el-table-column>
			<el-table-column
				prop="title"
				label="Ê≠åÂêç">
			</el-table-column>
			<el-table-column
				prop="channel_title"
				label="Ê≠åÊâã">
			</el-table-column>
			<el-table-column
				label="Êó∂Èïø">
				<template slot-scope="scope">
					<span>{{formatTime(scope.row.duration_in_seconds)}}</span>
				</template>
			</el-table-column>
				<el-table-column
					prop="played"
					label="Êí≠ÊîæÊï∞">
				</el-table-column>
			<el-table-column
				prop="artwork_url"
				width="120px"
				label="Â∞ÅÈù¢Âõæ">
				<template slot-scope="scope">
					<img :src="scope.row.artwork_url" alt="" @click="showBigImg(scope.row.artwork_url)" class="song_img">
				</template>
			</el-table-column>
			<el-table-column
				prop="original_id"
				label="Ê≠åÂçïÈìæÊé•">
				<template slot-scope="scope">
					<a :href="'https://www.youtube.com/watch?v='+scope.row.original_id" target="_blank" class="links">https://www.youtube.com/watch?v={{scope.row.original_id}}</a>
				</template>
			</el-table-column>
			<el-table-column
					fixed="right"
					label="Â§ÑÁêÜ"
					width="220">
					<template slot-scope="scope">
						 <el-button plain type="success" v-if="scope.row.status == 1" @click="editStatus(0,scope.row.track_id)">Â∑≤‰∏äÁ∫ø</el-button>
						 <el-button plain type="danger" v-if="scope.row.status == 0" @click="editStatus(1,scope.row.track_id)">Â∑≤‰∏ãÁ∫ø</el-button>
						 <el-button plain type="warning" @click="editSong(scope.row)">Áºñ Ëæë</el-button>
					</template>
				</el-table-column>
		</el-table>
		<el-pagination
			:current-page="currentPage"
			background
			@current-change="handleCurrentChange"
			layout="prev, pager, next"
			:total="total">
		</el-pagination>
				<!-- Êü•ÁúãbannerÂ§ßÂõæ -->
			<el-dialog
					title="Êü•ÁúãÂ§ßÂõæ"
					width="500px"
					:visible.sync="dialogVisible">
					<img :src="dialogImg" style="width: 100%;">
					<span slot="footer" class="dialog-footer">
						<el-button type="danger" @click="dialogVisible = false">ÂÖ≥ Èó≠</el-button>
					</span>
				</el-dialog>
				<!-- ‰∏ãÁ∫øÊ≠åÂçï -->
				<el-dialog
					width="500px"
					:title="title"
					:visible.sync="addVisible">
					<span slot="footer" class="dialog-footer">
						<el-button type="danger" @click="pushStatus()" :loading="loading2">Á°Æ ÂÆö</el-button>
					</span>
				</el-dialog>
				<!-- ‰øÆÊîπ/Ê∑ªÂä†Ê≠åÂçï -->
				<el-dialog
					:title="'ÁºñËæëÊ≠åÊõ≤'"
					:visible.sync="visible2">
					<el-form label-width="80px">
							<el-form-item label="Ê≠åÊõ≤ID">
								<el-input :disabled="true" v-model="song.id"></el-input>
							</el-form-item>
							<el-form-item label="Ê≠åÊõ≤ÂêçÁß∞" prop="title">
								<el-input v-model="song.title"></el-input>
							</el-form-item>
							<el-form-item label="Ê≠åÊâã">
								<el-input v-model="song.channel_title"></el-input>
							</el-form-item>
					</el-form>
					<span slot="footer" class="dialog-footer">
						<el-button type="success" @click="visible2 = false" :loading="loading2">Âèñ Ê∂à</el-button>
						<el-button type="danger" @click="pushSong()" :loading="loading2">Á°ÆÂÆö</el-button>
					</span>
				</el-dialog>
	</div>
</template>
<script type="text/javascript">
	import http from '../../utils/http.js';
	require('../../utils/common.js');
	export default {
		data() {
			return {
				songList:[],
				dialogVisible: false,
				dialogImg :'',
				addVisible: false,
				loading2: false,
				visible2: false,
				currentPage: 1,
				id:'',
				total: 0,
				idolname: '',
				Stitle: '',
				Sstatus: '',
				title: '',
				Stag: '',
				hosts: 'http://opt.groupy.cn/api/fileupload',
				Status: 1,
				idolType: 'idol',
				song: {
					id: '',
					title: '',
					channel_title: '',
				},
			}
		},
		methods: {
			formatTime(value) {
				if(value) {
	        var secondTime = parseInt(value);
	        var minuteTime = 0;
	        var hourTime = 0;
	        if(secondTime > 60) {
	          minuteTime = parseInt(secondTime / 60);
	          secondTime = parseInt(secondTime % 60);
	          if(minuteTime > 60) {
	            hourTime = parseInt(minuteTime / 60);
	            minuteTime = parseInt(minuteTime % 60);
	          }
	        }
	        var result = "" + parseInt(secondTime) + "Áßí";
	        if(minuteTime > 0) {
	          result = "" + parseInt(minuteTime) + "ÂàÜ" + result;
	        }
	        if(hourTime > 0) {
	          result = "" + parseInt(hourTime) + "Â∞èÊó∂" + result;
	        }
	        return result;
				}else {
					return '';
				}
  		},
			handleImg(res, file) {
					this.song.img = res.url;
			},
			tableRowClassName({row, index}) {
					if(row.status == '0') {
						return 'del-row';
					}
						return '';
			},
			showBigImg(val) {
				this.dialogImg = val;
				this.dialogVisible = true;
			},
			editStatus(val,id) {
        this.addVisible = true;
        this.Status = val;
        this.id = id;
        if(val == 1) {
        	this.title = `‰Ω†Á°ÆÂÆöË¶Å‰∏äÁ∫øID‰∏∫${id}Ëøô‰∏™Ê≠åÂêóÔºüÔºü`
        }else {
        	this.title = `‰Ω†Á°ÆÂÆöË¶Å‰∏ãÁ∫øID‰∏∫${id}Ëøô‰∏™Ê≠åÂêóÔºüÔºü`
        }
      },
      pushStatus() {
        var self = this;
        self.loading2 = true;
        http.get('/api/mfm_track/edit_status',{
        	params: {
        		id: self.id,
        		status: self.Status
        	}
        }).then(function(res){
	      	self.loading2 = false;
        	if(res.success) {
        		self.handleCurrentChange(self.currentPage);
		        self.addVisible = false
		      	if(self.Status == 0) {
		      		self.$message({
			          message: 'üò≠Ôºå‰Ω†ÂÜç‰πü‰∏ç‰ºöÂú®appÈáåÈù¢ÁúãÂà∞Ëøô‰∏™Ê≠å‰∫ÜÔºÅ',
			          type: 'success'
			        });
		      	}else {
			      	self.$message({
			          message: 'üòÉÔºå‰Ω†ÂæàÂø´Â∞±ÂèØ‰ª•Âú®appÈáåÈù¢ÁúãÂà∞Ëøô‰∏™Ê≠å‰∫ÜÔºÅ',
			          type: 'success'
			        });
		      	}
        	}else {
        		self.$message.error('ÊúçÂä°Âô®ÊàñËÄÖÁΩëÁªúÈîôËØØ'+ res);
        	}
      	}).catch(err => {
      		self.loading2 = false;
      		self.$message.error('ÊúçÂä°Âô®ÊàñËÄÖÁΩëÁªúÈîôËØØ'+ res);
      	})
      },
      editSong(obj) {
      	this.visible2 = true;
      	if(obj) {
      		this.song = {
	  			  id: obj.track_id,
	  			  title: obj.title,
	          channel_title: obj.channel_title,
      		}
      	}else {
      		this.song = {
      	     id: '',
      		  title: '',
	          channel_title: '',
      		}
      	}
      },
      pushSong() {
        var self = this;
        self.loading2 = true;
        if(self.song.title && self.song.channel_title) {
	        let data_ = {
	        	id: self.song.id,
            channel_title: self.song.channel_title,
            title: self.song.title,
	        }
	        http.post('/api/mfm_track/edit',data_).then(function(res){
		      	self.loading2 = false;
	        	if(res.success) {
	        		self.handleCurrentChange(self.currentPage);
			        self.visible2 = false
		      		self.$message({
			          message: 'üòÑ ‰Ω†ÂæàÂø´Â∞±ÂèØ‰ª•ÁúãÂà∞‰Ω†ÁºñËæëÁöÑËøô‰∏™Ê≠å‰∫ÜÔºÅÔºÅ',
			          type: 'success'
			        });
	        	}else {
	        		self.$message.error('ÊúçÂä°Âô®ÊàñËÄÖÁΩëÁªúÈîôËØØ'+ res);
	        	}
	      	}).catch(err => {
	      		self.loading2 = false;
	      		self.$message.error('ÊúçÂä°Âô®ÊàñËÄÖÁΩëÁªúÈîôËØØ'+ res);
	      	})
        }else {
        	self.$message({
	          message: 'üò° ÈÇ£‰πàÂ§ßÁöÑÁ©∫Áúã‰∏çÂà∞ÂêóÔºüÔºü',
	          type: 'warning'
	        });
        }
      },
			handleCurrentChange(val) {
				var self = this;
				if(!val) {
					self.currentPage = 1;
				}else{
					self.currentPage = val;
				}
				console.log(self.Stitle)
				http.get('/api/latest/list',{
					params: {
						pageNum: 10,
						page: self.currentPage,
						title: self.Stitle,
            status: self.Sstatus,
					}
				}).then(function(res){
					console.log(res)
					self.songList = res;
				}).catch(err => {
					self.$message.error('ÊúçÂä°Âô®ÊàñËÄÖÁΩëÁªúÈîôËØØ'+ res);
				})
				http.get('/api/latest/count',{
						params: {
								nums: self.Snums
						}
				}).then(function(res){
						console.log(res)
						self.total = res[0].count;
				}).catch(err => {
						self.$message.error('ÊúçÂä°Âô®ÊàñËÄÖÁΩëÁªúÈîôËØØ'+ res);
				})
			},
		},
		created: function(){
			var self = this;
			self.hosts = `http://${location.hostname}:4000/api/fileupload`;
				 self.handleCurrentChange();
		}
	}
</script>



